export default {
	data() {
		return {
			title: 'Hello',
			userInfo: {}
		}
	},
	onLoad() {
		this.userInfo = uni.getStorageInfoSync('userInfo') || {}
	},
	created() {
	},
	methods: {
		getUserInfo() {
			uni.getUserInfo({
				success: (res) => {
					this.userInfo = res.userInfo
					uni.setStorageSync('userInfo', this.userInfo)
				}
			})
		},
		goToPage() {
			uni.navigateTo({
				url: '/pages/editInfo/editInfo'
			});
		}
	}
}
